<template>
  <div class="login">
    <h1>潭州前端答题后台管理系统</h1>
    <el-form ref="form" :model="form" label-width="80px" :rules="rules" :ref="form" status-icon>
      <el-form-item label="用户名" prop="username">
        <el-input v-model="form.username" placeholder="请输入用户名" aria-required="请输入用户名" autocomplete="false"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="form.password" placeholder="请输入密码" show-password aria-required="请输入密码" autocomplete="false"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit(form)">登录</el-button>
        <el-button @click="onReset(form)">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'login',
  data: function () {
    function validateUsername (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        callback()
      }
    }
    function validatePassword (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          {validator: validateUsername, trigger: 'blur'}
        ],
        password: [
          {validator: validatePassword, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    onSubmit (form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          console.log(this.form)
          axios.get('http://192.168.0.104:8030/api/login', {
            params: {user: this.form.username, password: this.form.password}
          }).then(res => {
            // TODO 跳转
            if (res.data.code === 0) {
              this.$router.push('/admin')
              this.$message.success('登录成功')
            } else {
              console.log(res)
              this.$message.error(res.data.message)
            }
          })
        } else {
          this.$message.error('oops~填写信息有误')
          return false
        }
      })
    },
    onReset (form) {
      this.$refs[form].resetFields()
    }
  }
}
</script>

<style>
.login {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 50px;
  width: 600px;
  height: 400px;
  background-color: rgba(180,180,180,0.5);
  transform: translate(-50%, -50%);
  color: #eee;
  font-weight: bold;
  text-shadow: 0 0 1px #eee;
}

h1{
  text-align: center;
  margin-bottom: 60px;
}
</style>



